@import '../../../styles/common/breakpoints';
@import '../../../styles/common/colors';
@import '../../../styles/common/fonts';
@import '../../../styles/common/help';
@import '../../../styles/common/overrides';
@import '../../../styles/common/variable';

.totop {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 120px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  z-index: 1101;
  cursor: pointer;
  background-color: transparent;
  transition: background-color 0.2s;
  color: white;

  &:hover {
    background-color: rgba(0, 0, 0, 0.1);
  }
}
